
@import url('https://fonts.googleapis.com/css2?family=Lobster&family=Press+Start+2P&display=swap');
*{
  font-family: 'Press Start 2P', cursive;
}
div.container{
  text-align: center;
  padding: 20px;
  margin: 5vh auto;
  border: 2px solid black;
  background-color:white;
  color: black;
  width: 50%;
  border-radius: 10px;

 
}
body{
  background-color: crimson;
}
h1{
  font-weight: normal;
}
input{
  outline: none;
  padding: 10px 5px;
  border-radius: 5px;
  background-color: white;
  box-shadow: 2px 2px 2px black;
  width: 65%;
  font-size: 19px;
  margin: 30px auto;
}
p{
  font-weight: bold;
  
  font-size: 50px;
}
img{
  width: 60%;
}
.loader {
  border: 16px solid #f3f3f3; /* Light grey */
  border-top: 16px solid crimson; /* Blue */
  border-radius: 50%;
  width: 90px;
  height: 90px;
  background:url("./assets/pokeball.png");
  background-size: cover;
  animation: spin 1.5s linear infinite;
  margin: 0px auto;

}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}
.container-loader{
  width: 100%;
  height: 100vh;
  top: 0%;
  position: absolute;
 display: flex;
 justify-content: center;
 text-align: center;
 flex-direction: column;
 background-color: rgba(255, 255, 255, 0.8);
}

@media  (max-width:600px){
  div.container{
    width: 100%;
  }
  .container-loader p{
    font-size: 25px;
  }
}